.Calendar {
  max-width: 450px;
  margin: 5px auto;
  display: flex;
  box-shadow: 0px 5px 15px 0px rgba(204,204,204,1);
}

.Calendar-container {
  flex-grow: 1;
}

.Calendar-current-date {
  padding: 10px;
  border: 1px solid #aaa;
}

.Calendar-weeks {
  display: flex;
  border: 1px solid #aaa;
  border-top: 0px;
  height: 35px;
}

.Calendar-weeks > div {
  display: flex;
  flex-grow: 1;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.Calendar-days {
  display: grid;
  grid-template-columns: repeat(7, calc(calc(100%/7) - 1px));
  grid-auto-rows: 40px;
  grid-gap: 1px;
}

.Calendar-days > div {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.Calendar-days > div.is-disabled {
  opacity: 0.6;
}

.DriversList {
  display: flex;
  justify-content: center;
  margin-top: 15px;
}

.Driver-photo {
  width: 50px;
  height: 50px;
  border: 1.5px solid transparent;
  border-radius: 50%;
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2em;
  color: #111;
  box-shadow: 0px 5px 15px 0px rgba(204,204,204,0.75);
}
.BalanceGraph {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 25px;
}

.BalanceGraph > div {
  box-shadow: 0px 5px 15px 0px rgba(204,204,204,0.7);
}
.App {
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background:  #ddd;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-container {
  margin: 0 auto;
  width: 100%;
  max-width: 1024px;
  flex-grow: 1;
  height: auto;
  background: #eee;
}

.Modal {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.Modal-container {
  background: white;
  padding: 25px;
  box-shadow: 0px 5px 15px 0px rgba(0,0,0,0.75);
}

.Modal-container > :not(button) {
  margin-bottom: 20px;
}

.Modal-container > button {
  padding: 10px 25px;
  border-radius: 5px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}
